img {
    display: block;
    max-width: 100%;
    height: auto, 100%;
}

body {
    font-style: normal;
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 12px;
    letter-spacing: 0.08em;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

.no-scroll {
    overflow: hidden;
}

.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}